import React, { useEffect, useState } from "react";
import { Route, Routes, Navigate, useNavigate } from "react-router-dom";
import { SnackbarProvider } from "notistack";
import { Modal, Box, Button, Typography } from "@mui/material";
import Login from "./pages/Login";
import Home from "./pages/Home";
import TitleUpdater from "./components/TitleUpdater";
import PaginaNotFound from "./pages/PaginaNotFound";
import PrivateRoute from "./components/PrivateRoute";
import Ciudadanos from "./pages/Ciudadanos";
import TimerOffIcon from "@mui/icons-material/TimerOff";
import DataProtection from "./pages/DataProtection";
function App() {
  const navigate = useNavigate(); // Coloca el hook fuera del return
  const [isSessionExpired, setIsSessionExpired] = useState(false); // Estado para manejar el modal

  useEffect(() => {
    const checkTokenExpiration = () => {
      const token = localStorage.getItem("token");
      const expirationTime = localStorage.getItem("tokenExpiration");
      const currentTime = new Date().getTime();

      if (token && expirationTime && currentTime > expirationTime) {
        // Si el token ha expirado, mostramos el modal
        localStorage.removeItem("token");
        localStorage.removeItem("tokenExpiration");
        setIsSessionExpired(true);
      }
    };

    // Comprobacin al montar el componente (cuando recargas la pgina)
    checkTokenExpiration();

    // Intervalo para comprobar cada minuto
    const interval = setInterval(() => {
      checkTokenExpiration();
    }, 60000); // Verifica cada minuto

    return () => clearInterval(interval); // Limpia el intervalo al desmontar el componente
  }, []);

  const handleLogout = () => {
    // Eliminar los datos del localStorage y redirigir a login
    localStorage.removeItem("token");
    localStorage.removeItem("tokenExpiration");
    setIsSessionExpired(false); // Cierra el modal
    navigate("/login", { replace: true }); // Redirige a la pgina de login
  };

  return (
    <SnackbarProvider
      maxSnack={3}
      anchorOrigin={{
        vertical: "top",
        horizontal: "right",
      }}
      autoHideDuration={3500}
    >

      <Routes>
        <Route path="/login" element={<Login />} />
        <Route path="/" element={<Navigate to="/login" />} />
        <Route
          path="/login"
          element={
            <>
              <TitleUpdater title="Login - POINT" />
              <Login />
            </>
          }
        />
        <Route
          path="/*"
          element={
            <>
              <TitleUpdater title="Pgina no encontrada - POINT" />
              <PaginaNotFound />
            </>
          }
        />

        {/* Rutas protegidas */}
        <Route
          path="/nueva-consulta"
          element={
            <PrivateRoute>
              <TitleUpdater title="Nueva Consulta - POINT" />
              <Home />
            </PrivateRoute>
          }
        />
        <Route
          path="/ciudadanos"
          element={
            <PrivateRoute>
              <TitleUpdater title="Ciudadanos Almacenados - POINT" />
              <Ciudadanos />
            </PrivateRoute>
          }
        />

        <Route
          path="/proteccion-datos"
          element={
            <PrivateRoute>
              <TitleUpdater title="Proteccin de Datos - POINT" />
              <DataProtection />
            </PrivateRoute>
          }
        />
      </Routes>

      {/* Modal de sesin expirada */}
      <Modal open={isSessionExpired} onClose={() => { }}>
        <Box
          sx={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            width: 400,
            bgcolor: "background.paper",
            borderRadius: "8px",
            boxShadow: 24,
            p: 4,
            textAlign: "center",
          }}
        >
          <Typography variant="h6" component="h2">
            Sesin terminada <TimerOffIcon />
          </Typography>
          <Typography sx={{ mt: 2 }}>
            Tu sesin ha expirado. Haz clic en "Aceptar" para volver a iniciar
            sesin.
          </Typography>
          <Button
            onClick={handleLogout}
            variant="contained"
            sx={{
              mt: 3,
              backgroundColor: "#2d3689", // Color de fondo morado
              color: "#ffffff", // Color del texto
              "&:hover": {
                backgroundColor: "#212863", // Color de fondo al pasar el mouse
                color: "#ffffff", // Color del texto
              },
            }}
          >
            Aceptar
          </Button>
        </Box>
      </Modal>
    </SnackbarProvider>
  );
}

export default App;
